import React, { PureComponent, Fragment } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import {
  ListItem,
  ListItemText,
  withWidth,
  InputAdornment,
} from '@material-ui/core';
import ThreePropsMenu from './searchSelectorComponents/ThreePropsMenu';
import SearchSelectWrapper from './searchSelectorComponents/SearchSelectWrapper';
import AdditionalInfoDialog from './AdditionalInfoDialog';
import SearchIcon from '@material-ui/icons/Search';

/* eslint-disable-next-line no-extend-native */
String.prototype.has = function(value) {
  return this.toLowerCase().includes(value);
};

class SearchSelector extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      searchInput: '',
      selected: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      anchorEl: null,
      anchorElWidth: '',
      filteredData: [],
    };
    this.fieldRef = React.createRef();
  }

  componentDidMount() {
    const { required } = this.props;
    this.interval = null;
    this.setState({
      validators: required ? ['required'] : [],
      errorMessages: required ? ['จำเป็นต้องกรอก'] : [],
    });

    setTimeout(() => {
      this.forceUpdate();
    }, 500);
  }

  componentWillUnmount() {
    clearTimeout(this.interval);
  }

  handleBlur = e => {
    e.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 200);
  };

  handleSeachInput = e => {
    e.preventDefault();
    const { value } = e.target;
    const { currentTarget } = e;

    this.setState({
      searchInput: value,
      selected: false,
      anchorEl: currentTarget.parentElement,
      anchorElWidth: currentTarget.parentElement.offsetWidth,
      filteredData: this.filterData(value),
    });
  };

  filterData = input => {
    const lowerCasedInput = input.toLowerCase();
    const {
      codeFirst,
      firstDataProp,
      secondDataProp,
      thirdDataProp,
      maxDigits,
      infoData,
    } = this.props;

    if (codeFirst) {
      return lowerCasedInput.length < maxDigits + 1
        ? infoData.filter(datum =>
            datum[firstDataProp].has(lowerCasedInput)
          )
        : infoData.filter(datum =>
            datum[secondDataProp].has(lowerCasedInput)
          );
    } else {
      return infoData.filter(datum =>
        thirdDataProp
          ? datum[secondDataProp].has(lowerCasedInput) ||
            datum[thirdDataProp].has(lowerCasedInput) ||
            datum[firstDataProp].has(lowerCasedInput)
          : datum[firstDataProp].has(lowerCasedInput) ||
            datum[secondDataProp].has(lowerCasedInput)
      );
    }
  };

  handleSelect = e => {
    e.preventDefault();
    const { maxDigits, required, validators, errorMessages } = this.props;
    this.setState({
      selected: true,
      searchInput: e.currentTarget.innerText.trim().slice(0, maxDigits),
      validators: required ? validators : [],
      errorMessages: required ? errorMessages : [],
    });
  };

  render() {
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const {
      id,
      label,
      helperText,
      placeholder,
      firstDataProp,
      secondDataProp,
      thirdDataProp,
      required,
      width,
      dialogTitleTH,
      dialogFormat,
      dialogContent,
      dialogWidth,
      disabled,
    } = this.props;

    const {
      searchInput,
      validators,
      errorMessages,
      selected,
      anchorEl,
      anchorElWidth,
      filteredData,
    } = this.state;

    return (
      <Fragment>
        <TextValidator
          id={id}
          name={id}
          disabled={disabled}
          fullWidth
          required={required}
          label={label}
          helperText={helperText}
          placeholder={placeholder}
          onBlur={handleBlur}
          style={{ marginBottom: width === 'xs' ? '8px' : '10px' }}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
          variant="outlined"
          margin={width === 'xs' ? 'normal' : 'dense'}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon fontSize="small" />
              </InputAdornment>
            ),
            ref: this.fieldRef,
            endAdornment: dialogContent && (
              <InputAdornment position="end">
                <AdditionalInfoDialog
                  maxWidth={dialogWidth ? dialogWidth : 'xs'}
                  titleTH={dialogTitleTH ? dialogTitleTH : helperText}
                  titleEN={label}
                  format={dialogFormat}
                >
                  {dialogContent}
                </AdditionalInfoDialog>
              </InputAdornment>
            ),
          }}
        />
        {selected || searchInput === '' ? null : (
          <SearchSelectWrapper
            anchorEl={anchorEl}
            anchorElWidth={anchorElWidth}
          >
            {filteredData.slice(0, 20).map((datum, i) => (
              <ListItem
                button
                key={i}
                onClick={handleSelect}
                value={datum[firstDataProp]}
              >
                {thirdDataProp ? (
                  <ThreePropsMenu
                    first={datum[firstDataProp]}
                    second={datum[secondDataProp]}
                    third={datum[thirdDataProp]}
                  />
                ) : (
                  <ListItemText
                    primary={datum[firstDataProp]}
                    secondary={datum[secondDataProp]}
                  />
                )}
              </ListItem>
            ))}
          </SearchSelectWrapper>
        )}
      </Fragment>
    );
  }
}

export default withWidth()(SearchSelector);
